<template>
  <section class="deal" id="event">
    <div class="content">
      <h3 class="title" data-aos="fade-down" data-aos-deplay="150">
        {{ title }} <b>{{ title_orange }}</b>
      </h3>
      <p data-aos="fade-right" data-aos-deplay="300">{{ text }}</p>

      <div class="count-down" data-aos="flip-up" data-aos-deplay="450">
        <div
          v-for="(count, index) in contentItems"
          :key="`count-${index}`"
          :class="count.class"
        >
          {{ countEvent() }}

          <h3 :id="count.id">{{ count.id_tx }}</h3>
          <span> {{ count.text }} </span>
        </div>
      </div>
    </div>

    <!-- discount -->

    <div class="discount">
      <carousel
        :autoplay="true"
        :autoHeight="true"
        :margin="15"
        :responsive="responsive"
      >
        <!-- <template slot="prev"><span class="prev">prev</span></template> -->

        <div
          v-for="(box, index) in boxItem"
          :key="`box-${index}`"
          :class="box.id"
         
        >
          <img :src="box.image" alt="" />
          <h3>{{ box.name }}</h3>
          <div class="price">
            <span class="price-old"> {{ box.price_new }} </span>
            <span class="price-new"> {{ box.price_old }} </span>
          </div>

          <div
            v-for="(popup, index) in popUp"
            :key="`popup-${index}`"
            :class="popup.id"
          >
            <a class="btn-popup"> {{ popup.text }}</a>
          </div>
        </div>

        <!-- <template slot="next"><span class="next">next</span></template> -->
      </carousel>
    </div>

    <!-- end discount  -->
  </section>
</template>

<script>
import carousel from "vue-owl-carousel";

export default {
  data() {
    return {
      // ========== CONTENT ==========
      title: " Deals Of The ",
      title_orange: "Weeks",
      text: " The sale day has come, what are you waiting for, look for healthy foods in this discount. ",

      contentItems: [
        {
          class: "box",
          id: "day",
          id_tx: "00",
          text: "day",
        },

        {
          class: "box",
          id: "hour",
          id_tx: "00",
          text: "hour",
        },

        {
          class: "box",
          id: "minute",
          id_tx: "00",
          text: "minute",
        },

        {
          class: "box",
          id: "second",
          id_tx: "00",
          text: "second",
        },
      ],

      // ========== DISCOUNT ==========

      responsive: {
        0: { items: 2.1, nav: false },
        600: { items: 3.1, nav: true },
        900: { items: 3.2, nav: true },
      },

      boxItem: [
        {
          id: "box",
          image: require("../components/image/cr.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/chanh.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/db.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/cr.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/chanh.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/cr.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/db.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },

        {
          id: "box",
          image: require("../components/image/chanh.png"),
          name: "Chanh Ti",
          price_new: "666.66",
          price_old: "999.99",
        },
      ],

      popUp: [
        {
          id: "list-model",
          text: "popup",
        },
      ],

      // ========= END  =========
 
    };
  },

  methods: {
    countEvent() {
      let countDate = new Date("june 30, 2022 00:00:00").getTime();

      function countDown() {
        let now = new Date().getTime();
        let gap = countDate - now;

        let second = 1000;
        let minute = second * 60;
        let hour = minute * 60;
        let day = hour * 24;

        let d = Math.floor(gap / day);
        let h = Math.floor((gap % day) / hour);
        let m = Math.floor((gap % hour) / minute);
        let s = Math.floor((gap % minute) / second);

        document.getElementById("day").innerText = d;
        document.getElementById("hour").innerText = h;
        document.getElementById("minute").innerText = m;
        document.getElementById("second").innerText = s;
      }

      setInterval(function () {
        countDown();
      }, 1000);
    },
  },
  components: { carousel },
};
</script>

<style lang="scss">
* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

html {
  font-size: 62.5%;

  &::-webkit-scrollbar {
    display: none;
  }
}

.deal {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(./image/deal.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  min-height: 100vh;
  // position: relative;

// ========== CONTENT ==========
  .content {
    max-width: 60rem;
    padding: 2rem 0;

    .title {
      color: #fff;
      font-size: 4rem;

      b {
        color: #ff6924;
      }
    }

    p {
      text-transform: none;
      font-size: 1.8rem;
      padding: 1rem 0;
      color: #fff;
    }

    .count-down {
      display: flex;
      gap: 1rem;
      padding-right: 0.5rem;
      margin: 2rem 0;

      .box {
        border-radius: 0.3rem;
        text-align: center;
        padding: 0.7rem 1.5rem;
        background: #ff6924;
        color: #fff;

        h3 {
          font-size: 3rem;
        }

        span {
          font-size: 1.5rem;
        }
      }
    }
  }


// ========== DISCOUNT ==========
  .discount {
    max-width: 50rem;
    max-height: 20rem;
    width: 100%;
    align-items: center;
    justify-content: center;

    .box {
      position: relative;
      background: #fff;
      border-bottom-right-radius: 5rem;
      border-top-left-radius: 1rem;
      border-top-right-radius: 1rem;
      border: 0.1rem solid rgba(138, 138, 138, 0.2);
      padding: 2rem;
      overflow: hidden;

      img {
        width: 100%;
        max-width: 100%;
      }

      h3 {
        font-size: 1.7rem;
        color: 000039;
      }

      .price {
        display: flex;

        .price-old {
          text-decoration: line-through;
          color: #ff7800;
          font-size: 1.3rem;
          padding-right: 0.5rem;
        }

        .price-new {
          color: #000039;
          font-size: 1.7rem;
        }
      }

      &:hover {
        cursor: pointer;
      }
    }

    .list-model {
      position: absolute;
      bottom: -100%;
      left: 0;
      right: 0;
      border-bottom-right-radius: 5rem;
      border-top-left-radius: 5rem;
      background: #ff7800;
      padding: 1.1rem 0;
      text-align: center;

      .btn-popup {
        color: #fff;
        font-size: 1.7rem;
        text-transform: none;
      }
    }

    .box:hover .list-model {
      bottom: 0;
      cursor: pointer;
      transition: 0.5s ease-in-out;
    }

    .owl-nav {
      display: none;
    }

    .owl-stage {
      padding-bottom: 2rem;
    }

    .owl-theme .owl-dots .owl-dot.active span,
    .owl-theme .owl-dots .owl-dot:hover span {
      background: #ff7800;
    }
  }
}

@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }
}

@media (max-width: 991px) {
  section {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
}

@media (max-width: 678px) {
  html {
    font-size: 50%;
  }

  .deal {
    .content {
      padding: 1rem 5%;
      text-align: center;

      .count-down {
        justify-content: center;
      }
    }
  }
}

@media (max-width: 450px) {
  html {
    font-size: 50%;
  }
}
</style>
